<template>
  <div class="auth_wrap">
    <div class="header">
      <div class="head">
        <a @click="$router.replace('/user')" class="back"></a>
        {{ $t('lang.auth') }}
      </div>
    </div>
    <div class="verify_wrap">
		<div class="Tips" v-if="data.is_auth === 'U'">{{$t('lang.auth_wait')}}</div>
		<div class="Tips" v-if="data.is_auth === 'F'">
			{{$t('lang.auth_fail')}}
			<div class="txt">{{$t('lang.fail_r')}}：{{data.authFailed}}</div>
		</div>
		
      <form class="form" @submit.prevent="handleSubmit">
        <div class="tips">{{ $t('lang.auth_first') }}</div>
        <div class="item">
          <span class="info">{{ $t('lang.realname') }} </span>
          <input
            class="inp"
            type="text"
            name="name"
            v-model.trim="data.name"
            :placeholder="$t('lang.real_name')"
            :readonly="data.is_auth === 'Y'"
          />
        </div>
        <div class="item">
          <span class="info">{{ $t('lang.idcard') }} </span>
          <input
            class="inp"
            type="text"
            name="idcard"
            v-model.trim="data.idcard"
            :placeholder="$t('lang.input_idcard')"
            :readonly="data.is_auth === 'Y'"
          />
        </div>
		<div class="item" v-if="!fromtype">
			<span class="info">{{$t('lang.contract_title5')}}</span>
			<div class="cardImg" v-if="data.is_auth === 'Y' || data.is_auth === 'U'">
				<div class="h_img">
					<img class="Img" :src="data.card1" alt="" v-if="card1" />
				</div>
				<div class="h_img">
					<img class="Img" :src="data.card2" alt="" v-if="card2" />
				</div>
			</div>
			<div class="cardImg" v-else>
				<div class="h_img">
					<div v-if="!card1">
						<span>+</span>
						<div>{{$t('lang.contract_title5A')}}</div>
					</div>
					<img class="Img" :src="data.card1" v-if="card1" />
					<input type='file' name='card1' @change='uploadImg' v-if="data.is_auth == 'N' || data.is_auth == 'F'" />
				</div>
				<div class="h_img">
					<div v-if="!card2">
						<span>+</span>
						<div>{{$t('lang.contract_title5A')}}</div>
					</div>
					<img class="Img" :src="data.card2" v-if="card2" />
					<input type='file' name='card2' @change='uploadImg' v-if="data.is_auth == 'N' || data.is_auth == 'F'" />
				</div>
				<!-- <van-uploader :before-read="beforeRead1" :after-read="afterRead1" :max-count="1">
					<div class="h_img" icon="photo" type="primary">
						<div v-if="!card1">
							<span>+</span>
							<div>{{$t('lang.contract_title5A')}}</div>
						</div>
						<img class="Img" :src="data.card1" alt="" v-if="card1" />
					</div>
				</van-uploader> -->
				<!-- <van-uploader :before-read="beforeRead2" :after-read="afterRead2" :max-count="1">
					<div class="h_img" icon="photo" type="primary">
						<div v-if="!card2">
							<span>+</span>
							<div>{{$t('lang.contract_title5B')}}</div>
						</div>
						<img class="Img" :src="data.card2" alt="" v-if="card2" />
					</div>
				</van-uploader> -->
			</div>
		</div>
        <div class="tishi">
          <img src="./images/tishi.png" alt="" />
          <p>{{ $t('lang.tips') }}</p>
        </div>
        <div class="warming">
          {{ $t('lang.auth_notice') }}
        </div>
        <button
          type="submit"
          class="btn basic_btn_v2"
          :class="data.name != '' && data.idcard != '' ? '' : 'no_touch'"
          v-if="data.is_auth == 'N' || data.is_auth == 'F'"
        >
          {{ $t('lang.auth_r') }}
        </button>
      </form>
    </div>
    <transition name="fade">
      <div class="mask" v-show="rz_success">
        <div class="rz_inner">
          <img src="./images/renzhengchenggong.png" alt="" />
          <p>{{ $t('lang.auth_success') }}</p>
          <div class="basic_btn_v2" @click="rz_success_handle">
            {{ $t('lang.confirm') }}
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { Uploader } from 'vant';
import axios from 'axios';
Vue.use(Uploader);

export default {
  name: 'index',
  components: {},
  data() {
    return {
      data: {
		  card1:'',
		  card2:''
	  },
      money_info: '',
      money: '',
      rz_success: false,
	  card1:'',
	  card2:null,
	  fromtype:null,
    };
  },
  created() {
    this.$parent.footer(false);
	this.fromtype = this.$route.query.type;
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
		Fetch('/user/info/info').then(res => {
			this.data = res.data;
			if(this.data.id_card_pic){
				var obj = JSON.parse(this.data.id_card_pic);
				if(obj.card1 && obj.card1 !='_' && obj.card2 && obj.card2 !='_'){
					this.data.card1= obj.card1;
					this.card1= obj.card1;
							
					this.data.card2= obj.card2;
					this.card2= obj.card2;
				}else{
					this.data.card1= '_';
					this.card1= '';
							
					this.data.card2= '_';
					this.card2= '';
				}
				
			}
			
		});
    },
    rz_success_handle() {
      this.rz_success = false;
      this.$router.replace({
        path: this.$router.history.current.query.redirect || '/user',
      });
    },
	beforeRead1(file) {
		if (file.type !== 'image/jpeg' && file.type !== 'image/png') {
			this.$notify({
				message: this.$t('lang.type_limit'),
				color: '#FF3232',
				background: '#FFEAEA',
			});
			return false;
		}
		return true;
	},
	beforeRead2(file) {
		if (file.type !== 'image/jpeg' && file.type !== 'image/png') {
			this.$notify({
				message: this.$t('lang.type_limit'),
				color: '#FF3232',
				background: '#FFEAEA',
			});
			return false;
		}
		return true;
	},
	afterRead1(file) {
		let formData = new FormData();
		formData.append('file', file.file);
		formData.append('ssid', localStorage.getItem('ssid'));
		axios.post('/api/api/upload', formData).then(r => {
			if (r.data.code === 200) {
				this.$notify({
					background: '#EAF1FF',
					color: '#3292FF',
					message: this.$t('lang.edit_success'),
				});
				this.data.card1 = r.data.data.file;
				this.card1 = r.data.data.file;
				console.log(this.data);
			} else {
				this.$notify({
					message: this.$t('lang.upload_fail'),
					color: '#FF3232',
					background: '#FFEAEA',
				});
			}
		});
	},
	afterRead2(file) {
		console.log(file);
		let formData = new FormData();
		formData.append('file', file.file);
		formData.append('ssid', localStorage.getItem('ssid'));
		axios.post('/api/api/upload', formData).then(r => {
			if (r.data.code === 200) {
				this.$notify({
					background: '#EAF1FF',
					color: '#3292FF',
					message: this.$t('lang.edit_success'),
				});
				this.data.card2 = r.data.data.file;
				this.card2 = r.data.data.file;
			} else {
				this.$notify({
					message: this.$t('lang.upload_fail'),
					color: '#FF3232',
					background: '#FFEAEA',
				});
			}
		});
	},
    uploadImg(event){
		
		let imgObj = event.target.files[0];
		let name = event.target.name;
		
		let formdata = new FormData();
		formdata.append('file', imgObj);
		formdata.append('ssid', localStorage.getItem('ssid'));
		axios.post('/api/api/upload', formdata).then(r => {
			if (r.data.code === 200) {
				this.$notify({
					background: '#EAF1FF',
					color: '#3292FF',
					message: this.$t('lang.edit_success'),
				});
				this.data[name] = r.data.data.file;
				this[name] = r.data.data.file;
			} else {
				this.$notify({
					message: this.$t('lang.upload_fail'),
					color: '#FF3232',
					background: '#FFEAEA',
				});
			}
		});
	},
	handleSubmit() {
      if (!this.data.name) {
        this.$notify({
          message: this.$t('lang.input_name'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.data.idcard) {
        this.$notify({
          message: this.$t('lang.input_idcard'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }
		if (!this.data.card1 && !this.fromtype) {
		  this.$notify({
		    message: this.$t('lang.contract_title5C'),
		    color: '#FF3232',
		    background: '#FFEAEA',
		  });
		  return;
		}
		if (!this.data.card2 && !this.fromtype) {
		  this.$notify({
		    message: this.$t('lang.contract_title5D'),
		    color: '#FF3232',
		    background: '#FFEAEA',
		  });
		  return;
		}
		if(this.fromtype && (this.data.card1 == ''||this.data.card2=='')){
			this.data.card1 = '/';
			this.data.card2 = '/';
		}
		Fetch('/user/info/auth', {
			...this.data,
		}).then(() => {
			this.rz_success = true;
		});
    },
  },
};
</script>

<style lang="less" scoped>
.Tips{
	text-align: center;font-size: 18px;padding: 10px;line-height:24px;
	background: #FFEAEA;color: #FF3232;
	.txt{font-size: 14px;;}
}
.auth_wrap {
  background: #f7f8f9;
  min-height: 100vh;
}
.verify_wrap .form {
	width: 100%;
	padding: 20px 25px;
	background-color: #ffffff;
	// margin-top: 12px;
	.tips {
		width: 100%;
		height: 21px;
		line-height: 28px;
		font-size: 17px;
		color: #000000;
		margin-bottom: 20px;
		font-weight: 400;
	}
	.cardImg{
		display:flex;align-items: center;justify-content: space-between;
	}
	.h_img{
		width: 160px;margin: 0 auto;position: relative;
		height: 160px;text-align: center;
		background: #F1F1F1;
		border-radius: 5px;font-size: 14px;color: #999999;
		display:flex;align-items: center;justify-content: center;
		span{font-size: 72px;}
		img{width: 30px;margin-bottom: 20px;}
		.Img{width: 100px;height: 100px;margin-bottom: 0;}
		input{position:absolute;top:0;left:0;width:100%;height:100%;opacity: 0;}
	}
}

.verify_wrap .form .item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  // -webkit-box-align: center;
  // -ms-flex-align: center;
  // align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  border-bottom: 1px solid #e3e4e5;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 22px;
  padding: 8px 0;
  background-color: #ffffff;
}
.item .info {
  color: #b1b1b1;
  margin-bottom: 10px;
  width: 105px;
}
.verify_wrap .form .item .inp {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  text-align: left;
  font-size: 15px;
}

.warming {
  font-size: 13px;
  line-height: 18px;
  color: #b1b1b1;
  margin-top: 13px;
}

.btn {
  display: block;
  width: 90.6%;
  height: 47px;
  line-height: 47px;
  margin-top: 61px;
}

.rz_inner {
  width: 77.6%;
  padding: 39px 60px 26px 60px;
  background-color: #ffffff;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  margin: 0 auto;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 80px;
  }
  p {
    font-size: 17px;
    color: #000;
    line-height: 24px;
    margin: 32px auto 39px auto;
  }
  div {
    height: 40px;
    line-height: 40px;
    width: 100%;
  }
}
// .header .head{
//     background: url(./images/zxbg.png) center no-repeat;
//   }
.tishi {
  margin-top: 13px;
  display: flex;
  align-items: center;
  img {
    margin-right: 8px;
    width: 15px;
    height: 15px;
  }
  p {
    font-size: 13px;
    color: #333;
  }
}
.basic_btn_v2 {
  border-radius: 25px !important;
  background: linear-gradient(to right, #22bfbe, #19c099) !important;
}
</style>
